@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@900&family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.light-theme{background-color:#fefcfd;color:#1f1c22}.light-theme .light-accent{color:#ed6b86;transition:color 0.3s linear}.light-theme a:hover{color:#1f1c22}.light-theme .toggle{background-color:#ed6b86}.dark-theme{background-color:#1f1c22;color:#fefcfd}.dark-theme .dark-accent{color:#14e1bf;transition:color 0.3s linear}.dark-theme a:hover{color:#fefcfd}.dark-theme .toggle{background-color:#14e1bf}*{transition:all 0.3s linear;transition:background-color 0.2s linear}*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}.app{height:100vh;padding-top:8rem;padding-bottom:10rem;padding-left:10rem}.app-description{width:28rem}.app-icons{padding:1rem 0;display:flex;grid-gap:1rem;gap:1rem}.icon{cursor:pointer}.sub-title{font-size:3rem;line-height:3rem;font-weight:900;font-family:"Montserrat"}.text{font-size:1rem;line-height:1.6rem;font-weight:400;font-family:"Nunito";padding:1rem 0}.title{font-size:3rem;line-height:3rem;font-weight:900;font-family:"Montserrat"}.toggle{border-radius:1rem;cursor:pointer;height:2rem;width:4rem}.toggle-ball{background-color:#fefcfd;transition:0.2s;position:relative;height:2rem;width:2rem;border-radius:2rem}.toggle-off{left:0;transform:translateX(100%)}.toggle-on{left:2rem;transform:translateX(-100%)}

